<template>
  <div>
    <b-table striped hoverable :data="table_data" :columns="columns" />
  </div>
</template>

<script>
export default {
  name: 'FhirEncounters',
  props: {
    patients: Array,
  },
  computed: {
    columns() {
      return [
        {
          field: 'type', label: 'Afspraak', sortable: true,
        },
        {
          field: 'reason', label: 'Reden', sortable: true,
        },
        {
          field: 'date', label: 'Datum', sortable: true,
        },
        {
          field: 'zkh', label: 'ZKH', sortable: true,
        },
      ];
    },
    table_data() {
      let allResources = [];
      for (const patient of this.patients) {
        const resources = patient.entry.filter(
          (e) => e.resource.resourceType.toLowerCase() === 'encounter',
        ).map((res) => {
          const mapped = res;
          mapped.zkh = patient.zkh;
          return mapped;
        });

        allResources = allResources.concat(resources);
      }
      console.log(JSON.stringify(allResources));

      const data = allResources.map((cp) => ({
        type: this.type(cp.resource),
        reason: this.reason(cp.resource),
        date: this.date(cp.resource),
        zkh: cp.zkh,
      }));

      return data;
    },
  },

  methods: {
    type(resource) {
      return resource.type[0].coding[0].display;
    },
    reason(resource) {
      return resource.reason[0].coding[0].display;
    },
    // eslint-disable-next-line no-unused-vars
    date(resource) {
      let period = '';
      if (resource.period.start) {
        period = new Date(resource.period.start).toLocaleString();
      }
      return period;
    },
  },
};
</script>
