<template>
  <div v-if="loading">
    <b-loading :is-full-page="true" v-model="loading" ></b-loading>
  </div>
  <div v-else>
    <tavi-steps :patients="patients" />
    <br />
    <b-tabs>
      <b-tab-item label="Behandelplan" icon="user-tag">
        <fhir-careplan :patients="patients" />
      </b-tab-item>
      <b-tab-item label="Verrichtingen" icon="procedures">
        <fhir-procedures :patients="patients" />
      </b-tab-item>
      <b-tab-item>
        <template slot="header">
          <b-icon icon="calendar-check" />
          <span>
            Afspraken
            <!-- <b-tag rounded class="is-info">{{ labwaarden.length }}</b-tag> -->
          </span>
        </template>
        <fhir-encounters :patients="patients" />
      </b-tab-item>
      <b-tab-item>
        <template slot="header">
          <b-icon icon="file-alt" />
          <span>
            Vragenlijsten
            <!-- <b-tag rounded class="is-info">{{ labwaarden.length }}</b-tag> -->
          </span>
        </template>
        <fhir-questionnaires />
      </b-tab-item>
      <!-- <b-tab-item disabled>
        <template slot="header">
          <b-icon icon="x-ray" />
          <span>
            Echo's
          </span>
        </template>
        <fhir-observations :observations="labwaarden" />
      </b-tab-item> -->
      <b-tab-item>
        <template slot="header">
          <b-icon icon="vial" />
          <span>
            Labwaarden
            <b-tag rounded class="is-info">{{ labwaarden.length }}</b-tag>
          </span>
        </template>
        <fhir-observations :observations="labwaarden" />
      </b-tab-item>
      <b-tab-item disabled>
        <template slot="header">
          <b-icon icon="mail-bulk" />
          <span>
            Correspondentie
          </span>
        </template>
        <fhir-observations :observations="labwaarden" />
      </b-tab-item>
      <b-tab-item>
        <template slot="header">
          <b-icon icon="database" />
          <span>
            NHR
            <!-- <b-tag rounded class="is-info">{{ labwaarden.length }}</b-tag> -->
          </span>
        </template>
        <fhir-nhr :patients="patients" />
      </b-tab-item>
    </b-tabs>
  </div>
</template>

<script>
import FhirCareplan from '@/components/FhirCareplan.vue';
import FhirProcedures from '@/components/FhirProcedures.vue';
import FhirEncounters from '@/components/FhirEncounters.vue';
import FhirNhr from '@/components/FhirNhr.vue';
import FhirObservations from '@/components/FhirObservations.vue';
import TaviSteps from '@/components/TaviSteps.vue';
import FhirQuestionnaires from '@/components/FhirQuestionnaires.vue';
import smartClient from '@/smartClient';
import Service from '@/service';

const service = new Service();

export default {
  name: 'Home',
  components: {
    FhirCareplan,
    FhirProcedures,
    FhirNhr,
    FhirEncounters,
    FhirObservations,
    TaviSteps,
    FhirQuestionnaires,
  },
  data() {
    return {
      patient: undefined,
      patients: [],
      labwaarden: [],
      seeAlso: [],
      vitalSigns: [],
      loading: true,
      error: null,
    };
  },
  async mounted() {
    const vm = this;
    try {
      const smart = await smartClient();
      // console.log(smart);

      // smart.patient.read().then((fhirPatient) => {
      //   console.log(fhirPatient);
      //   vm.patient = fhirPatient;
      // });

      // const patients = await service.getPatient('ba64ed28-db37-46a6-a8f6-69c695772fde');
      const patients = await service.getPatient(smart.patient.id);

      console.log(patients);
      this.loading = false;

      let allObservations = [];

      for (const patient of patients) {
        const observations = patient.entry
          .filter((e) => e.resource.resourceType === 'Observation')
          .map((obs) => {
            const mapped = obs;
            mapped.zkh = patient.zkh;
            return mapped;
          });

        allObservations = allObservations.concat(observations);
      }

      vm.labwaarden = allObservations.filter(
        (obs) => obs.resource.category[0].coding[0].display === 'laboratory',
      );

      vm.patients = patients;
    } catch (resp) {
      console.error(resp);
      this.loading = false;
      this.error = resp.error || resp;
    }
  },
};
</script>
