<script>
export default {
  name: 'TaviSteps',
  props: {
    patients: Array,
  },
  data() {
    return {
      activeStep: 3,
      isAnimated: true,
      isRounded: true,
    };
  },
  computed: {
    hoofdbehandelaar() {
      console.log(this.patients);
      let allPractitioners = [];

      for (const patient of this.patients) {
        const resources = patient.entry.filter(
          (e) => e.resource.resourceType.toLowerCase() === 'practitioner',
        ).map((res) => {
          const mapped = res;
          mapped.zkh = patient.zkh;
          return mapped;
        });
        allPractitioners = allPractitioners.concat(resources);
      }
      console.log(allPractitioners);
      if (allPractitioners && allPractitioners.length > 0) {
        const practitioner = allPractitioners[0];

        const parts = [];
        if (practitioner.resource.name[0].prefix) {
          parts.push(practitioner.resource.name[0].prefix);
        }

        if (practitioner.resource.name[0].given) {
          parts.push(practitioner.resource.name[0].given);
        }

        if (practitioner.resource.name[0].family) {
          parts.push(practitioner.resource.name[0].family);
        }

        return parts.join(' ');
      }
      return '---';
    },
  },
};
</script>
