class Service {
    getPatientFromIndex = async function getPatientFromIndex(patientId) {
      // console.log(patientId);
      const url = `https://patientindex.dhealth.usor.nl:7003/hapi-fhir-jpaserver/fhir/Patient/?identifier=${patientId}`;

      // console.log(url);
      return fetch(url)
        .then((res) => res.json());
    }

    getPatient = async function getPatient(patientId) {
      console.log(`getPatient: ${patientId}`);
      const url = `https://patientindex.dhealth.usor.nl:7003/hapi-fhir-jpaserver/fhir/Patient/?identifier=${patientId}`;

      const patientFromIndex = await fetch(url).then((res) => res.json());
      console.log(patientFromIndex);
      const seeAlsoLinks = [];
      for (const entry of patientFromIndex.entry) {
        console.log(entry);
        if (entry.resource && entry.resource.link) {
          for (const link of entry.resource.link) {
            console.log(link);
            if (link.type === 'seealso') {
              seeAlsoLinks.push(link.other.display);
            }
          }
        }
      }

      const seeAlsoPatients = [];
      for (const link of seeAlsoLinks) {
        const seeAlsoPatient = await fetch(link).then((res) => res.json());
        seeAlsoPatients.push(seeAlsoPatient);
      }

      const fullPatients = [];
      for (const patient of seeAlsoPatients) {
        if (patient.entry && patient.entry.length >= 1) {
          const { fullUrl } = patient.entry[0];
          const everything = `${fullUrl}/$everything?_count=200`;
          const fullPatient = await fetch(everything).then((res) => res.json());

          if (fullUrl.indexOf('martini') !== -1) {
            fullPatient.zkh = 'MZH';
          } else if (fullUrl.indexOf('umcg') !== -1) {
            fullPatient.zkh = 'UMCG';
          } else {
            fullPatient.zkh = '';
          }

          fullPatients.push(fullPatient);
        }
      }

      return fullPatients;
    }
}

export default Service;
