<template>
  <div>
    <b-table striped hoverable :data="table_data" :columns="columns" />
  </div>
</template>

<script>
export default {
  name: 'FhirCareplan',
  props: {
    patients: Array,
  },
  computed: {
    columns() {
      return [
        {
          field: 'category', label: 'Categorie', sortable: true,
        },
        {
          field: 'reason', label: 'Reden', sortable: true,
        },
        {
          field: 'period', label: 'Datum', sortable: true,
        },
        {
          field: 'zkh', label: 'ZKH', sortable: true,
        },
      ];
    },
    table_data() {
      let allCareplans = [];
      for (const patient of this.patients) {
        const resources = patient.entry.filter(
          (e) => e.resource.resourceType.toLowerCase() === 'careplan',
        ).map((res) => {
          const mapped = res;
          mapped.zkh = patient.zkh;
          return mapped;
        });

        allCareplans = allCareplans.concat(resources);
      }
      console.log(JSON.stringify(allCareplans));

      const data = allCareplans.map((cp) => ({
        category: cp.resource.category[0].text,
        reason: this.reason(cp.resource),
        period: this.periode(cp.resource),
        zkh: cp.zkh,
      }));

      return data;
    },
  },

  methods: {
    reason(careplan) {
      const text = careplan.activity.map((act) => `<li>${act.detail.code.text} <small>(${act.detail.status})</small> </li>`);
      return `<ul>${text.join``}</ul>`;
    },
    // eslint-disable-next-line no-unused-vars
    periode(careplan) {
      let period = '';

      if (careplan.period.start) {
        period = new Date(careplan.period.start).toLocaleString();
      }

      return period;
    },
  },
};
</script>
