<template>
  <div>
    <b-table striped hoverable :data="table_data" :columns="columns" />
  </div>
</template>

<script>
export default {
  name: 'FhirProcedures',
  props: {
    patients: Array,
  },
  computed: {
    columns() {
      return [
        {
          field: 'details', label: 'Details', sortable: true,
        },
        {
          field: 'date', label: 'Datum', sortable: true,
        },
        {
          field: 'zkh', label: 'ZKH', sortable: true,
        },
      ];
    },
    table_data() {
      let allResources = [];
      for (const patient of this.patients) {
        const resources = patient.entry.filter(
          (e) => e.resource.resourceType.toLowerCase() === 'procedure',
        ).map((res) => {
          const mapped = res;
          mapped.zkh = patient.zkh;
          return mapped;
        });

        allResources = allResources.concat(resources);
      }
      console.log(JSON.stringify(allResources));

      const data = allResources.map((cp) => ({
        details: this.details(cp.resource),
        date: this.date(cp.resource),
        zkh: cp.zkh,
      }));

      return data;
    },
  },

  methods: {
    details(resource) {
      return resource.code.coding[0].display;
    },
    // eslint-disable-next-line no-unused-vars
    date(resource) {
      let period = '';

      if (resource.performedPeriod.start) {
        period = new Date(resource.performedPeriod.start).toLocaleString();
      }

      return period;
    },
  },
};
</script>
