<template>
  <!-- eslint-disable max-len -->
  <div>
    <b-table :mobile-cards='false' sticky-header striped hoverable :data="table_data" :columns="columns"  />
  </div>
</template>

<script>
export default {
  name: 'FhirObservations',
  props: {
    observations: Array,
  },
  computed: {
    columns() {
      console.log(JSON.stringify(this.observations));

      const allDates = this.observations.map((obs) => (
        obs.resource.effectiveDateTime
      ));
      const uniqueDates = [...new Set(allDates)];
      const sortedDates = uniqueDates.sort((a, b) => (a < b ? -1 : 1));

      return [
        {
          field: 'observation',
          label: 'Bepaling',
          sticky: true,
          sortable: true,
          cellClass: 'is-sticky-column',
          headerClass: 'is-sticky-column',
        },
        ...sortedDates.map((d) => ({
          field: d.replace(/\D/g, ''),
          label: new Date(d).toLocaleString().replace(', ', '\r\n'),
        })),
      ];
    },
    table_data() {
      const allDates = this.observations.map((obs) => (
        obs.resource.effectiveDateTime
      ));
      const uniqueDates = [...new Set(allDates)];
      const sortedDates = uniqueDates.sort((a, b) => (a < b ? -1 : 1));

      // 1. sla de datastructuur plat
      const flattened = this.observations.map((obs) => ({
        observation: obs.resource.code.text,
        category: obs.resource.category[0].coding[0].display,
        value: this.value(obs),
        date: obs.resource.effectiveDateTime.replace(/\D/g, ''),
        zkh: obs.zkh,
      }));

      // 2. groupeer per bepaling
      const grouped = this.groupBy(flattened, 'observation');

      // 3. maak een object per bepaling, waarbij elke date een field is
      const data = Object.keys(grouped).map((key) => {
        const values = sortedDates.map((date) => ({
          key: date.replace(/\D/g, ''),
          value: this.valueForDate(grouped[key], date.replace(/\D/g, '')),
        }));

        const mapped = {
          observation: key,
        };

        values.forEach((v) => {
          mapped[v.key] = v.value;
        });

        return mapped;
      });
      console.log(data);
      return data;
    },
  },

  methods: {
    valueForDate(observations, date) {
      for (const o of observations) {
        if (o.date === date) {
          return o.value;
        }
      }
      return '';
    },
    value(observation) {
      if (observation.resource.valueQuantity) {
        return `${observation.resource.valueQuantity.value} ${observation.resource.valueQuantity.unit}`;
      }
      if (observation.resource.valueCodeableConcept) {
        return observation.resource.valueCodeableConcept.text;
      }
      if (observation.resource.code.coding[0].code === '85354-9') {
        const [diastolic, systolic] = observation.resource.component;
        return `${systolic.valueQuantity.value}/${diastolic.valueQuantity.value} ${systolic.valueQuantity.unit}`;
      }
      return '';
    },
    groupBy(xs, key) {
      return xs.reduce((rv, x) => {
        // eslint-disable-next-line no-param-reassign
        (rv[x[key]] = rv[x[key]] || []).push(x);
        return rv;
      }, {});
    },
  },
};
</script>

<style>
.is-sticky-column {
    /* background: #23d160 !important;
    color: white !important; */
    font-weight: bold;
}
.table-wrapper {
  height: calc(100vh - 350px) !important;
}
</style>
