<template>
  <div>
    <div class="navbar-menu">
      <div class="navbar-end">
        <button class="button is-success">
          <b-icon icon="file-excel" />
          <span>Export</span>
        </button>
      </div>
    </div>
    <section>
      <h1 class="subtitle">Identificerende variabelen</h1>
      <table class="table">
        <tr>
          <th>Patientnummer</th>
          <td>---</td>
        </tr>
        <tr>
          <th>Geboortedatum</th>
          <td>{{ birthdate }}</td>
        </tr>
        <tr>
          <th>Geslacht</th>
          <td>{{ gender }}</td>
        </tr>
      </table>
    </section>
    <br />
    <section>
      <h1 class="subtitle">Patient karakteristieken - EuroSCORE variabelen</h1>
      <table class="table">
        <tr>
          <th>Lengte</th>
          <td>160</td>
        </tr>
        <tr>
          <th>Gewicht</th>
          <td>72.8</td>
        </tr>
        <tr>
          <th>Diabetes mellitis</th>
          <td>30 (diabetes, insuline)</td>
        </tr>
        <tr>
          <th>Instabiele angina pectoris</th>
          <td>1 = ja</td>
        </tr>
      </table>
    </section>
  </div>
</template>

<script>
export default {
  name: 'FhirNhr',
  props: {
    patients: Array,
  },
  computed: {
    birthdate() {
      let allResources = [];
      for (const patient of this.patients) {
        const resources = patient.entry
          .filter((e) => e.resource.resourceType.toLowerCase() === 'patient')
          .map((res) => {
            const mapped = res;
            mapped.zkh = patient.zkh;
            return mapped;
          });

        allResources = allResources.concat(resources);
      }

      return new Date(allResources[0].resource.birthDate).toLocaleDateString();
    },
    gender() {
      let allResources = [];
      for (const patient of this.patients) {
        const resources = patient.entry
          .filter((e) => e.resource.resourceType.toLowerCase() === 'patient')
          .map((res) => {
            const mapped = res;
            mapped.zkh = patient.zkh;
            return mapped;
          });

        allResources = allResources.concat(resources);
      }

      return allResources[0].resource.gender;
    },
  },
};
</script>
